body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

canvas {
  display: block;
}

nav {
  position: absolute;
  width: 100%;
  height: auto;
  padding: 2rem 2rem;
  z-index: 9999;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

nav .socials img {
  width: 25px;
  height: auto;
  transition: 0.5s;
}

nav .socials :hover {
  color: #eac6ff;
  width: 30px;
  transition: 0.7s;
}

nav .logo {
  color: white;
  font-size: 14px;
  font-weight: 500;
}

nav .links {
  display: flex;
  flex-direction: column;
}

nav .links .link {
  color: white;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
}

nav .links .active {
  color: #eac6ff;
  font-style: italic;
}

nav .links :active {
  color: #eac6ff;
  font-style: italic;
}

@media screen and (min-width: 750px) {
  nav .socials {
    margin-right: 5rem;
  }
  nav .logo {
    color: white;
    font-size: 16px;
    font-weight: 500;
  }
  nav .links .link {
    font-size: 14px;
  }
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  margin-bottom: 100px;
}

.item .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-bottom: 2rem;
}

.item .content .itemTitle {
  font-family: "Questrial", sans-serif;
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 5px;
  margin-bottom: 1rem;
}

.item .content .itemTitle span {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  font-style: italic;
}

.item .content .itemTitle span .medium1 {
  color: #eac6ff;
}

.item .content .itemTitle span .medium2 {
  color: #c1f5ff;
}

.item .content .itemDescription {
  color: #fff;
  text-align: left;
  font-weight: 400;
  line-height: 24px;
  font-size: 14px;
  text-align: left;
  padding: 0 1rem;
  max-width: 360px;
}

.item .imageDiv {
  display: flex;
  flex-direction: column;
}

.item .imageDiv .itemImage {
  max-width: 400px;
  height: auto;
  margin-bottom: 5rem;
}

.item .imageDiv .itemButton {
  color: white;
  font-size: 16px;
  -ms-grid-row-align: center;
  align-self: center;
  text-decoration: none;
  border: 2px solid white;
  padding: 1rem;
  margin-bottom: 5rem;
}

.item .imageDiv :hover {
  font-size: 18px;
  transition: 0.5s;
}

@media screen and (min-width: 750px) {
  .item {
    flex-direction: row;
  }
  .item .content {
    margin-right: 2rem;
    width: auto;
    margin-bottom: 0rem;
  }
  .item .imageDiv {
    display: flex;
    flex-direction: column;
  }
  .item .imageDiv .itemImage {
    max-width: 400px;
    height: auto;
    margin-bottom: 2rem;
  }
  .item .imageDiv .itemButton {
    font-size: 16px;
    padding: 1rem;
    margin-bottom: 0rem;
  }
  .item .imageDiv :hover {
    font-size: 18px;
    transition: 0.5s;
  }
}

header {
  width: 100%;
  height: 100vh;
  min-height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

header .wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

header .wrapper .headerText {
  width: 80vmin;
  min-width: 300px;
  font-family: "Questrial", sans-serif;
  color: #ffffff;
  font-size: 4.5vmin;
  font-weight: 400;
  letter-spacing: 3px;
  line-height: 6vmin;
  text-align: center;
  margin-bottom: 2rem;
  overflow: hidden;
}

.headerButton {
  color: white;
  font-size: 3vmin;
  text-decoration: underline;
  margin-bottom: 5rem;
}

.aboutButton {
  color: white;
  font-size: 16px;
  -ms-grid-row-align: center;
  align-self: center;
  text-decoration: underline;
  margin-bottom: 4rem;
}

.arrow {
  height: auto;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  bottom: 2rem;
}

.arrow .arrowText {
  font-size: 2.2vmin;
  color: white;
  margin-bottom: 1rem;
}

.arrow .arrowIcon {
  -ms-grid-column-align: center;
      justify-self: center;
  width: 32px;
}

.projects {
  position: relative;
  width: 100%;
  height: 100% auto;
  min-height: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 2rem;
  text-align: center;
}

.about {
  height: 100vh;
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.about .aboutImage {
  position: absolute;
  width: 30vmin;
  height: auto;
  bottom: 0;
  right: 0;
}

.about .aboutTitle {
  font-family: "Questrial", sans-serif;
  color: white;
  margin-bottom: 2rem;
  font-weight: 400;
  letter-spacing: 5px;
  overflow: hidden;
  font-size: 5vmin;
}

.about .aboutText {
  color: white;
  width: 80vmin;
  font-size: 2.5vmin;
  font-weight: 400;
  line-height: 3vmin;
  -ms-grid-column-align: center;
      justify-self: center;
  text-align: center;
  letter-spacing: 1px;
  overflow: hidden;
  margin-bottom: 2rem;
}

.reactCredit {
  display: flex;
  align-items: center;
}

.reactCredit .creditText {
  color: white;
  font-size: 2vmin;
  font-style: italic;
}

.reactCredit .creditLogo {
  margin-left: 0.5rem;
  width: 4vmin;
}

@media screen and (min-width: 750px) {
  .about .aboutText {
    font-size: 3vmin;
    line-height: 3.5vmin;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  list-style: none;
  text-decoration: none;
}

